import React from "react"
import {Link} from "react-router-dom"
import headerImg from "../../../jsons/images/bg_6.jpg"
import {gsap} from "gsap"
import {useIntersection} from "react-use"
const HeaderImg = ({title,section})  => {
    const headerImgRef = React.useRef(null)
    const observer = useIntersection(headerImgRef,{
        ref:null,
        rootMargin: '0px',
        threshold: .2
    })
    gsap.to(headerImgRef.current,{duration:0,y:150,opacity:0})
    observer && observer.intersectionRatio < .2 ? gsap.to(headerImgRef.current,{}) : gsap.to(headerImgRef.current,{duration:.4,y:0,opacity:1})
    return (
        <div style={{backgroundImage:`url(${headerImg})`}} className="header-img">
            <div className="ani" ref={headerImgRef}>
                <h6><Link to="/e-commerce-winkel">Home</Link> {section} </h6>
                <h2>{title}</h2>
            </div>
        </div>
    )
}


export default HeaderImg